  position: fixed;
  right: -90px;
  bottom: 15%;
  z-index: 100;
  transition: all 0.6s;
}
.style-switcher .style-switcher__control {
  position: absolute;
  top: 0;
  left: -50px;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background: #2d2d2d;
}
.style-switcher .style-switcher__control:after {
  content: "\f013";
  position: absolute;
  font-family: FontAwesome;
  font-size: 30px;
  line-height: 1;
  color: #fff;
  pointer-events: none;
  transition: all 1s;
  top: 50%;
  margin-top: -15px;
  left: 50%;
  margin-left: -15px;
}
